$color_1: rgba(255, 255, 255, 0.87);
$color_2: #646cff;
$color_3: #535bf2;
$color_4: #213547;
$color_5: #747bff;
$font-family_1: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
$font-family_2: inherit;
$background-color_1: #242424;
$background-color_2: #1a1a1a;
$background-color_3: #ffffff;
$background-color_4: #f9f9f9;
$border-color_1: #646cff;

:root {
	font-family: $font-family_1;
	line-height: 1.5;
	font-weight: 400;
	color-scheme: light dark;
	color: $color_1;
	background-color: $background-color_1;
	font-synthesis: none;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
a {
	font-weight: 500;
	color: $color_2;
	text-decoration: inherit;
	&:hover {
		color: $color_3;
	}
}
body {
	margin: 0;
	display: flex;
	place-items: center;
	min-width: 320px;
	min-height: 100vh;
}
h1 {
	font-size: 3.2em;
	line-height: 1.1;
}
button {
	border-radius: 8px;
	border: 1px solid transparent;
	padding: 0.6em 1.2em;
	font-size: 1em;
	font-weight: 500;
	font-family: $font-family_2;
	background-color: $background-color_2;
	cursor: pointer;
	transition: border-color 0.25s;
	&:hover {
		border-color: $border-color_1;
	}
	&:focus {
		outline: 4px auto -webkit-focus-ring-color;
	}
	&:focus-visible {
		outline: 4px auto -webkit-focus-ring-color;
	}
}
.card {
	padding: 2em;
}
#app {
	max-width: 1280px;
	margin: 0 auto;
	padding: 2rem;
	text-align: center;
}
@media (prefers-color-scheme: light) {
	:root {
		color: $color_4;
		background-color: $background-color_3;
	}
	a {
		&:hover {
			color: $color_5;
		}
	}
	button {
		background-color: $background-color_4;
	}
}
